import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, genNumId, qryStore, parse4Dig, genAlphDay, generateComprobanteId } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SelectDropAccount_Cmpt } from "..";
import { useNavigate } from "react-router";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { getLastSequence } from "../services/server_fetching";

const Svg = SvgIcon_Cmpt();

const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const DropInput = SelectDropAccount_Cmpt();


const AccountLabelDetail = AccountLabelDetail_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();


const frmNm = "new_operation_comprobante"

const AddComprobanteManual  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};

	

	const save  = async ()  => {
		let ffm = getStore(frmNm);

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}

		let comp2save = [];


		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] = comprobanteId;

		let arr = getStore("accountListOnCmp") && ObjectKeys(getStore("accountListOnCmp"));
		arr && arr?.map(async (element, indE)=>{
			let nnfrm = {...ffm};
			let dataAcc = {...getStore("accountListOnCmp")[element]};
			nnfrm["account"] = dataAcc["account"];
			let accountD = getStore("account_ids")?.[dataAcc["account"]];
			if(accountD.payOrCollect){
				nnfrm["payOrCollect"] = accountD.payOrCollect;
			}
			nnfrm["subAccount"] = dataAcc["subAccount"];
			nnfrm["type"] = dataAcc["type"];
			nnfrm["document"] = dataAcc["document"].trim();
			nnfrm["amount"] = dataAcc["amount"]*1;
			nnfrm["operationId"] = gen16CodeId();
			
			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;


			comp2save.push(bdyq2)
			
			
		});


		// console.log(comp2save)

		
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
		await Promise.all(promises);
		
		data?.modalID && CloseModal({id:data?.modalID});
		
		setTimeout(() => {
			updStore("accountListOnCmp", null)
			updStore(frmNm, null)
			set_obs(gen16CodeId());
			confirm && confirm();
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
			
		}, 400);
		/** */
		///let pdfUri = await generateTranslateAddComprobanteManual(ffm, true, true);
		///window.open(pdfUri);
	};





	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	
	const handleSearchAccount = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		updStore("accountPicker", nn);
		set_obs(gen16CodeId())
	}


	const editSearchAccount = () => {
		updStore("accountPicker", null);
		set_obs(gen16CodeId())
	}
	const updFrmAccounts = (f, v, k) => {
		let frm2 = getStore("accountPicker") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("accountPicker", frm2);
		set_obs(gen16CodeId())
	};

	const addAccount2List = () => {
		let frm2 = {...getStore("accountPicker")} || {};
		let rstFrm = {...getStore("accountPicker")} || {};
		let accountListOnCmp = {...getStore("accountListOnCmp")} || {};
		let id = frm2["account"] + "_"+ frm2["subAccount"]+ "_"+ frm2["document"];
		accountListOnCmp[id] = frm2;
		updStore("accountListOnCmp", accountListOnCmp);
		rstFrm["document"] = "";
		rstFrm["amount"] = "";
		rstFrm["type"] = "";
		updStore("accountPicker", rstFrm);
		set_obs(gen16CodeId())
	}

	const rmvAccount2List = (id) => {
		let accountListOnCmp = getStore("accountListOnCmp") || {};
		delete accountListOnCmp[id];
		updStore("accountListOnCmp", accountListOnCmp);
		set_obs(gen16CodeId())
	}

	let accountD = getStore("account_ids")?.[getStore("accountPicker")?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore("accountPicker")?.subAccount];
	
	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "760px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Asiento Manual
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				
			</div>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>

			

			

			<div className="boxAddAcc" style={{margin: "18px 0"}}>

				{getStore("accountPicker")?.account ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}}>
								{accountD?.code} 
							</div>
							<div className="name">
								{accountD?. name}
							</div>
						</div>
						<div className="flexSpace"/>
						{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					</div>
					<div className="flexSpace"/>
					<div className="icon" onClick={editSearchAccount}>
						<Svg name={"outline_edit"} color={"#1a73e8"}/>
					</div>
				</div> :
				<DropInput 
					
					placeholder={"buscar una cuenta"} 
					width={420}
					handleClick={handleSearchAccount}
				/>
				}
				{getStore("accountPicker")?.account ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className="flexSpace"/>
					<CheckBox 
						data={[
							{label: "Debito", id:"debit"},
							{label: "Credito", id:"credit"}
							
						]}
						active={getStore("accountPicker")?.["type"]}
						updSelect={(e)=>updFrmAccounts("type",e)}
					/>
					<div className="flexSpace"/>
				</div>
				:null}
				{getStore("accountPicker")?.account && getStore("accountPicker")?.type ? 
				<div className="_dsplFlx " >
					<TextInput  
						init={getStore("accountPicker")?.["document"]} 
						label={"Documento"} 
						width={120} 
						updChanges={(e)=>updFrmAccounts("document",e)}
					/>
					<div className="" style={{maxWidth: 130 , marginLeft: 7}}>
						<TextInput  
							init={getStore("accountPicker")?.["amount"]} 
							label={"Importe"} 
							width={120} 
							type={"number"}
							updChanges={(e)=>updFrmAccounts("amount",e)}
						/>
					</div>
					
					<div className="flexSpace"/>
					{getStore("accountPicker")?.amount && getStore("accountPicker")?.document ? 
					<div className="card">
						<button onClick={addAccount2List}>
							Agregar
						</button>
					</div>
					:null}
				</div>
				:null}
				<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
					<div className="tr_bar tr_bar_20">
						Cuenta
					</div>
					<div className="tr_bar tr_bar_20">
						Sub Cuenta
					</div>
					<div className="tr_bar tr_bar_20">
						Documento
					</div>
					<div className="tr_bar tr_bar_20">
						Debito
					</div>
					<div className="tr_bar tr_bar_20">
						Credito
					</div>
				</div>
				{getStore("accountListOnCmp") && ObjectKeys(getStore("accountListOnCmp"))?.map((element, indE)=>
					<ListingAccounts key={element} idKey={element} data={getStore("accountListOnCmp")[element]} deleteItem={rmvAccount2List}/>
				)}
			</div>

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{EstaCuadrado(getStore("accountListOnCmp"))?
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default AddComprobanteManual;


const ListingAccounts  = ({ data, deleteItem, idKey }) => {
	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];

	const editSearchAccount = () => {
		deleteItem(idKey)
	}
	
	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_20">
				<AccountLabelDetail 
					data={accountD}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar tr_bar_20">
				<AccountLabelDetail 
					data={subaccountD}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
			</div>
			<div className="tr_bar tr_bar_20">
				{data?.document ? data.document : "" }
			</div>
			<div className="tr_bar tr_bar_20">
				{data?.type === "debit" ? data.amount : 0 }
			</div>
			<div className="tr_bar tr_bar_20">
				{data?.type === "credit" ? data.amount : 0 }
			</div>
			<div className="flexSpace"/>
			<div className="icon" onClick={editSearchAccount}>
				<Svg name={"outline_delete"} color={"#c62828"}/>
			</div>

		</div>
	)

}




const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		
		console.log(amount)
		balance = amount + parseFloat(newB);
		console.log(balance)
	});
	return arr && arr?.length>0 && balance === 0;
}  