import { fetchEncryptStream, updStore, getStore, gen16CodeId, convertArray2ObjGroupby, convertArray2Obj, qryStore } from "../../../stores/Utils";

export const getall_entries = async (cb) => {
    let bdyq2 = getStore(qryStore())?.all_entries;
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId
    }

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    // console.log(JSON.stringify(td));
    if (td) {
        updStore("entries_list", td);
        updStore("entries_ids", convertArray2Obj(td,"accountingEntryId"));
        cb && cb();
    }
};
 




export const search_entries = async (cb) => {
    let bdyq2 = getStore(qryStore())?.search_entries;
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":search": getStore("search_entry_qry")
    }

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("entries_list", td);
        cb && cb();
    }
};



export const delete_entryParam = async (accountingEntryId, cb) => {
    
    let bdyq2 = getStore(qryStore())?.delete_entries;
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        accountingEntryId: accountingEntryId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        cb && cb();
    }
};



export const getLastSequence  = async (cb)  => {
    const res = fetchEncryptStream({query:"getLastSequence"});
    const td = await res;
    if (td) {
        //cb && cb(td)
        return td;
    }
}